@charset "utf-8";
@import url("https://cdn.jsdelivr.net/npm/bulma@0.9.1/css/bulma.min.css");

html,
body {
  font-family: "Noto Sans", sans-serif;
  scroll-behavior: smooth !important;
  transition: all 0.25s ease;
}

/* restyle */
hr {
  height: 1px;
  background-color: #0a0a0a;
  margin: 1rem 0;
}
.columns:not(.is-mobile) > .column {
  &:not(:last-child) {
    @media screen and (max-width: 768px) {
      padding-bottom: 0 !important;
    }
  }
}
.title {
  letter-spacing: 0.07em;
  &:not(:last-child) {
    margin-bottom: 1rem;
  }
  @media screen and (max-width: 768px) {
    letter-spacing: 0 !important;
    margin-bottom: 0 !important;
  }
}
.subtitle {
  letter-spacing: 0.2em;
  @media screen and (max-width: 768px) {
    letter-spacing: 0.1em !important;
  }
}
/* END restyle */

/* width */
::-webkit-scrollbar {
  width: 0.5vmax !important;
}

/* Track */
::-webkit-scrollbar-track {
  background: white !important;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #363636 !important;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: black !important;
}

::placeholder {
  font-family: "Noto Sans", sans-serif;
}

#hero {
  background: rgb(0, 0, 0);
  background: -moz-linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.75) 100%);
  background: -webkit-linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.75) 100%);
  background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.75) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#000000", GradientType=1);

  &::before {
    // filter: blur(0.15rem);
    background-image: url($main-hero-image);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    position: absolute;
    opacity: 0.25;
    content: "";
  }
}

#navbar {
  .navbar-burger {
    span:nth-child(2) {
      left: calc(50%);
      width: 8px;
    }
  }

  .navbar-dropdown {
    border: none;
    right: 0;
    left: auto;
  }

  a.navbar-item:hover,
  .navbar-link:hover {
    background-color: rgba($color: #fff, $alpha: 0.25) !important;
    color: white !important;
  }

  a.navbar-item.is-active {
    background-color: rgba($color: #fff, $alpha: 0.25) !important;
    color: white !important;
    font-weight: 600;

    &:hover {
      background-color: rgba($color: #fff, $alpha: 0.25) !important;
      color: grey;
    }
  }

  .navbar-link:not(.is-arrowless)::after {
    border-color: grey;
  }
}

#about {
  .image {
    max-width: 20vmax;
    margin: auto;

    img {
      padding: 4px;
      border: 3px solid #0a0a0a;
      // box-shadow: 0 0 0.5em 0 rgba($color: #000, $alpha: 0.25);
    }
  }

  a {
    min-width: 25vmax;
  }
}

#contact {
  .contact-button {
    margin: 0.5rem auto;
    display: flex;
    justify-content: center;
    max-width: 24rem;
    overflow: hidden;
  }

  .sm-icon {
    transition: all 0.15s ease-in-out;
    margin: auto 0.75vmax;

    &:hover {
      transform: scale(1.25);
      filter: opacity(0.75);
    }
  }

  hr {
    background-color: #0a0a0a;
  }
}

#footer {
  #backtotop {
    display: block;
    width: 10rem;
    margin: auto auto 1rem auto;
    padding: 10px 0.25rem 0.25rem 0.25rem;
    clip-path: polygon(100% 10px, 50% 0%, 0% 10px, 0% 100%, 100% 100%);
    transition: all 0.25s ease;

    &:hover {
      transform: scale(1.05);
    }
  }

  .content {
    margin: 0;
    padding: 1rem;
    line-height: 1.25;

    .column {
      margin: auto;
      display: grid;
      justify-content: center;

      a {
        color: #7a7a7a;

        &:hover {
          color: white;
        }
      }

      figure {
        border: 1.5px solid white;
        border-radius: 50rem;
        justify-content: center;
        margin: auto;
        transition: all 0.25s ease;
        &:hover {
          transform: scale(1.15);
        }
      }
    }
  }

  #credits {
    padding: 0.75rem;
    font-size: 0.85rem;

    a {
      text-decoration: underline;
      font-weight: bolder;
      color: white;
    }
  }
}

#blog {
  .post-card {
    transition: all 0.25s ease;
    transform: scale(0.9);
    line-height: normal;
    border-radius: 0.25rem;
    -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);

    &:hover {
      transform: scale(0.925);
      -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
      -moz-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
      box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
    }

    @media (max-width: 768px) {
      transform: scale(1);
      margin-bottom: 5vmax !important;

      &:hover {
        transform: scale(1.025);
        // margin-bottom: 5vmax !important;
        -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
        -moz-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
        box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
      }

      .image {
        padding-top: 50% !important;
      }
    }

    .title {
      margin: 0.5rem !important;
    }

    .content {
      margin: 0.5rem;
    }

    .column {
      display: grid;

      .image {
        border-radius: 0.25rem;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    a {
      display: inherit;
    }
  }
}

#post {
  @media (min-width: 768px) {
    margin: auto 2.5vmax;
  }
  a {
    text-decoration: underline;
  }
  #post-image {
    border-radius: 0.25rem;
  }
  .subtitle {
    @media (min-width: 768px) {
      padding: 0 1rem;
      letter-spacing: 0.05rem;
    }
  }

  #description {
    margin-bottom: 1.25vmax;
  }

  .tag {
    margin: 0.15rem auto;
  }
  .content {
    hr {
      background-color: grey;
    }
    svg {
      margin-right: 1rem;
    }
    img,
    iframe {
      margin: 0.25rem auto;
    }
  }

  .container {
    word-break: break-word;
  }
}

.container {
  .svg-inline--fa {
    margin-right: 0.25em;
  }
}

/*
 * My Bulma additions
 */
.p-2-mobile {
  @media screen and (max-width: 768px) {
    padding: 1rem 1rem !important;
  }
}
.mb-1-mobile {
  @media screen and (max-width: 768px) {
    margin-bottom: 0.5rem !important;
  }
}
.mv-1-mobile {
  @media screen and (max-width: 768px) {
    margin-top: 0.5rem !important;
    margin-bottom: 0.5rem !important;
  }
}

table {
  tbody {
    tr:nth-child(2n) {
      background-color: rgba($color: #000, $alpha: 0.05) !important;
    }
  }
}

.nobr {
  white-space: nowrap !important;
}

.is-overlay.is-decoration {
  display: block;
  position: fixed;
  overflow: hidden;
  pointer-events: none;
  z-index: 29 !important;
}

.pagination-link.is-current {
  pointer-events: none;
}

/*
 * Font Awesome Icons Via CSS Pseudo-elements
 */
.has-fa {
  .svg-inline--fa {
    margin-right: 0.3em;
  }
  .fa-file-pdf {
    color: indianred;
  }

  &:hover {
    .fa-file-pdf {
      color: firebrick;
    }
  }

  &::before {
    display: none;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
  }

  &.icon-map-marker::before,
  &.icon-map::before,
  &.icon-home::before,
  &.icon-heart::before,
  &.icon-clock::before,
  &.icon-phone::before,
  &.icon-email::before,
  &.icon-rss::before,
  &.icon-pdf::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }

  &.icon-tg::before,
  &.icon-fbm::before,
  &.icon-github::before {
    font-family: "Font Awesome 5 Brands";
    font-weight: 400;
  }

  &.icon-map-marker::before {
    content: "\f3c5";
  }
  &.icon-map::before {
    content: "\f279";
    font-weight: 400;
  }
  &.icon-home::before {
    content: "\f015";
  }
  &.icon-heart::before {
    content: "\f004";
  }
  &.icon-clock::before {
    content: "\f017";
  }
  &.icon-phone::before {
    content: "\f879";
  }
  &.icon-email::before {
    content: "\f0e0";
  }
  &.icon-rss::before {
    content: "\f143";
  }

  &.icon-pdf::before {
    content: "\f1c1";
  }

  &.icon-tg::before {
    content: "\f3fe";
  }
  &.icon-fbm::before {
    content: "\f39f";
  }
  &.icon-github::before {
    content: "\f09b";
  }
}

.fa-inst-logo {
  --var-size: 23%;
  width: var(--var-size);
  height: var(--var-size);
  position: absolute;
  top: calc(100% - var(--var-size));
  left: 0;
  color: #fafafa;
  background: radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  padding: 4px;
  border-radius: 25%;
  box-shadow: 0.1em 0.1em 1em 0 rgba($color: #000, $alpha: 0.15);

  svg {
    width: 100%;
  }
}
